import React from 'react'
import {useState,useEffect} from 'react'
import Nav from './nav'
import Footer from './footer'
import Cookies from 'js-cookie'
import {navigate} from 'gatsby'
import defaultVars from '../libs/config'
import {BarLoader} from 'react-spinners'
import {connect} from 'react-redux'
import Web3 from 'web3'
import {DashboardIcon,BonusIcon,LogoutIcon,UplineIcon,StatIcon,ReferralIcon,MediaIcon,AdminIcon} from './common'
import {injectIntl,Link} from 'gatsby-plugin-intl'
import { ToastContainer } from 'react-toastify';
import logo from '../images/logo-loading.svg';
import 'react-toastify/dist/ReactToastify.css';
import "./layout.css"
import "aos/dist/aos.css"
import contractAbi from '../libs/abi'
import SEO from './seo'
const web3 = new Web3(defaultVars.provider);
const contract = new web3.eth.Contract(contractAbi,defaultVars.contractAddress);
const prices = defaultVars.prices;
const refs = defaultVars.refs;
const incomes = defaultVars.incomes;
const bonusRewards = defaultVars.bonus;


const dbNav = [
    {path: '/db/dashboard/',icon: 'db',name: 'Dashboard',id: 'db.nav.db'},
    {path: '/db/media/',icon: 'media',name: 'Media',id: 'db.nav.media'},
    {path: '/db/stats/',icon: 'stats',name: 'Stats',id: 'db.nav.stats'},
    {path: '/db/referrals/',icon: 'ref',name: 'Referrals',id: 'db.nav.ref'},
    {path: '/db/uplines/',icon: 'upline',name: 'Uplines',id: 'db.nav.up'},
    // {path: '/db/admin/',icon: 'admin',name: 'Admin',id: 'db.admin'},
    // {path: '/db/bonus/',icon: 'bonus',name: 'Commission statistics',id: 'db.nav.bonus'},
    {path: '/db/logout',icon: 'logout',name: 'Logout',id: 'db.nav.logout'},
]

const Icons = (name) => {
    switch(name){
        case 'db':
            return <DashboardIcon />;
        case 'bonus': 
            return <BonusIcon />;
        case 'logout': 
            return <LogoutIcon />;
        case 'upline':
            return <UplineIcon />;
        case 'stats':
            return <StatIcon />;
        case 'ref':
            return <ReferralIcon />;
        case 'media':
            return <MediaIcon />;
        case 'admin':
            return <AdminIcon />;
    }
}
const NavItem = () => {
    return <div className=""></div>
}
const mapStateToProps = ({userInfo}) => {
    return {userInfo}
}
const mapDispatchToProps = dispatch => {
    return {
        setUserInfo: (userinfo)=>dispatch({type: 'SETUSERINFO',userinfo}),
        setLv: (lv)=>dispatch({type: 'SETLV',lv}),
        setLvs: (lvs)=>dispatch({type: 'SETLVS',lvs}),
        setTotal: (total)=>dispatch({type: 'SETTOTAL',total}),
        setTotalBonus: (totalBonus)=>dispatch({type: 'SETTOTALBONUS',totalBonus}),
        setRank: (rank)=>dispatch({type: 'SETRANK',rank})
    }
}
const DbLayout = ({intl,children,setUserInfo,setLv,setLvs,setTotal,setRank,setTotalBonus}) => {
    const [loading,setLoading] = useState(true);
    const [userAdmin,setUseraSAdmin] = useState(false);
    useEffect(()=>{
        const address = Cookies.get('user');
        const a = async()=>{
            if (address){
                const getUser = await contract.methods.us(address).call();
                if (getUser.x){
                    setUseraSAdmin(address); 
                    setLoading(false);
                } else {
                    Cookies.remove('user');
                    return navigate('/login/');
                }                
            } else {
                return navigate('/login/');
            }
        }
        a();
    },[null])
    if (loading){
        return (
            <div className="h-screen flex items-center justify-center">
                <div className="text-center">
                    <img src={logo} alt="" className="mb-2"/>                    
                    <div className="flex items-center justify-center">
                        <BarLoader width={200} height={4} color={'#24405c'}/>
                    </div>
                </div>
            </div>
        )
    }
    return (
        <>
            <Nav />
            <ToastContainer />
            <SEO title="Dashboard"/>
            <main style={{backgroundColor: '#f7f6f6'}}>
                <div className="container mx-auto px-2 lg:px-4 py-4 md:flex">
                    <div className="db-nav flex">
                        {dbNav.map((val,key)=>{
                            return (
                                <Link key={key} to={val.path} className="flex-1 flex items-center justify-center text-lights-bluebold font-sans font-semibold text-11 lg:text-14 lg:justify-start lg:py-2" activeClassName="text-lights-orangebold">
                                        <span className="md:mr-2 lg:mr-4">
                                            {Icons(val.icon)}
                                        </span>
                                        <span className="hidden md:inline">{intl.formatMessage({id: val.id,defaultMessage: val.name})}</span>
                                </Link>
                            )
                        })}
                    </div>
                    <div className="flex-1">{children}</div>
                </div>
            </main>
            <Footer />
        </>
    )
}

const DbLayoutWithRedux = connect(mapStateToProps,mapDispatchToProps)(DbLayout)
export default injectIntl(DbLayoutWithRedux);