import React from 'react'
import {useState,useEffect} from 'react'
import DbLayout from '../../components/dblayout'
import {CopyCom} from '../../components/common'
import defaultVars from '../../libs/config'
import Cookies from 'js-cookie'
import Web3 from 'web3'
import contractAbi from '../../libs/abi'

export default () => {
    const [userId,setUserId] = useState('undefined');
    useEffect(()=>{
        const address = Cookies.get('user');
        window.web3 = new Web3(defaultVars.provider);
        window.contract = new window.web3.eth.Contract(contractAbi,defaultVars.contractAddress);
        const a = async()=>{
            const user = await window.contract.methods.us(address).call();
            setUserId(user.a.toString())
        }
        a();
    })
    return (
        <DbLayout>
            <div className="p-2 lg:p-8 shadow bg-white">
                <div className="text-lights-orangebold text-16 font-bold font-sans text-center uppercase mb-8">Media</div>
                <div className="mb-4">
                    <div className="font-sans font-semibold text-14 text-lights-bluebold text-center mb-2">Banner 420x425</div>
                    <div className="text-center mb-2">
                        <img src="/img/media--01.png" alt="" className="mx-auto max-w-full"/>
                    </div>
                    <CopyCom text={`<a href="${defaultVars.link}/?ref=${userId}"><img src="${defaultVars.mainpage}/img/media--01.png" width="420px" height="425px"/></a>`} />
                </div>
                <div className="">
                    <div className="font-sans font-semibold text-14 text-lights-bluebold text-center mb-2">Banner 925x232</div>
                    <div className="text-center mb-2">
                        <img src="/img/media--02.png" alt="" className="mx-auto max-w-full"/>
                    </div>
                     <CopyCom text={`<a href="${defaultVars.link}/?ref=${userId}"><img src="${defaultVars.mainpage}/img/media--02.png" width="925px" height="425px"/></a>`} />
                </div>
            </div>
        </DbLayout>
    )
}